<template>
  <div class="tab">
    <el-tabs v-model="activeName" @tab-click="handleClick" style="font-size: 18px">
      <el-tab-pane label="订单记录" name="first"></el-tab-pane>
      <el-tab-pane label="提现记录" name="second"></el-tab-pane>
      <el-tab-pane label="学习记录" name="third"></el-tab-pane>
      <el-tab-pane label="被投诉记录" name="fourth"></el-tab-pane>
      <el-tab-pane label="信用值记录" name="five"></el-tab-pane>
      <el-tab-pane label="分销记录" name="six"></el-tab-pane>
    </el-tabs>
     <myTable :data="activeTableData" @getTableData='getTableDataAccept' :indexList='activeName' :colData='activeCols' :page='page' :perPage='per_page' :total='total'></myTable>
  </div>
</template>
<script>
import { getOrderService, getWithdrawService } from '../../service/AuntManage'
import myTable from './myTable.vue'
import {orderColsConfig,
    withdrawColsConfig,
    statusTempConfig,
    billStatusTempConfig,
    orderStatusTempConfig} from './AuntDetailConfig.js'
import {formatDate} from '../../assets/common/utils.js'
export default {
  props: ['id', 'optionsStatus'],
  components: {myTable},
  data () {
    return {
      total: 0,
      activeName: 'first',
      activeCols: [],
      activeTableData: [],
      orderData: [],
      withdrawDepositData: [],
      orderCols: orderColsConfig,
      withdrawCols: withdrawColsConfig,
      statusTemp: statusTempConfig,
      billStatusTemp: billStatusTempConfig,
      orderStatusTemp: orderStatusTempConfig,
      page: 1,
      per_page: 10
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    getTableDataAccept (page) {
      this.page = page
      if (this.activeName === 'first') {
        this.getOrderList()
      } else if (this.activeName === 'second') {
        this.getWithdrawList()
      } else if (this.activeName === 'third') {
      } else if (this.activeName === 'fourth') {
      } else if (this.activeName === 'five') {
      } else if (this.activeName === 'six') {
      }
    },
    getOrderList () {
      let params = {id: this.id, per_page: this.per_page, page: this.page}
      getOrderService(params).then((res) => {
        res.data.items.forEach((element, key) => {
          if (element.service_apply_time) {
            element.service_apply_time = formatDate(element.service_apply_time, 'yyyy-MM-dd')
          }
          if (element.create_time) {
            element.service_end_time = formatDate(element.service_end_time, 'yyyy-MM-dd')
          }
          if (element.create_time) {
            element.create_time = formatDate(element.create_time, 'yyyy-MM-dd')
          }
          if (element.order_object) {
            element.service_type = element.order_object.type_name
            element.user_name = element.order_object.user_name
          }
          element.status = this.orderStatusTemp[element.status]
        })
        this.activeTableData = res.data.items
        console.log(this.activeTableData)
        this.activeCols = this.orderCols
        this.total = res.data.total
      })
    },
    getWithdrawList () {
      let params = {id: this.id, per_page: this.per_page, page: this.page}
      getWithdrawService(params).then((res) => {
        res.data.items.forEach((element, key) => {
          // 转换日期格式
          element.create_time = formatDate(element.create_time, 'yyyy-MM-dd')
          element.update_time = formatDate(element.update_time, 'yyyy-MM-dd')
          if (element.withdraw_tag) {
            element.withdraw_tag = this.statusTemp[element.withdraw_tag]
          }
          if (element.withdraw_status) {
            element.withdraw_status = this.billStatusTemp[element.withdraw_status]
          }
          if (element.bank_card_objeck) {
            element.card_number = element.bank_card_objeck.card_number
          }
        })
        this.activeCols = this.withdrawCols
        this.activeTableData = res.data.items
        this.total = res.data.total
      })
    },
    // 底部记录列表切换
    handleClick (tab, event) {
      this.page = 1
      this.activeName = tab.name
      if (tab.name === 'first') {
        console.log(this.page)
        this.getOrderList()
      }
      if (tab.name === 'second') {
        this.getWithdrawList()
      }
    }
  }
}
</script>



