<template>
  <div>
    <span class="total">数据统计：共{{total}}条</span><br><br>
    <el-table
      :data="data"
      border
      style="width: 100%">
      <el-table-column
        v-for="(item, index) in colData"
        v-bind:key="index"
        :prop="item.field"
        :label="item.label"
        :index="item.index"
        align="center"
        :width="width">
        <template slot-scope="scope">
            {{scope.row[item.field]}}
            <!-- 订单 -->
          <el-button
            v-if="indexList === 'first' && item.field === 'name'"
            @click="lookOrderDetail(scope.row.id)"
            type="text" size="small">
              查看详情
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination-container">
      <el-pagination background
        layout="prev, pager, next"
        :total="total"
        prev-text="上一页"
        next-text="下一页"
        :current-page="page"
        @current-change="pageChange">
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  props: ['indexList', 'data', 'colData', 'width', 'page', 'perPage', 'total'],
  data () {
    return {}
  },
  watch: {
    page () {
      return this.page
    }
  },
  methods: {
    lookOrderDetail (id) {
      this.$router.push('/v2/serviceOrderDetail/' + id)
    },
    pageChange (val) {
      this.$emit('getTableData', val)
    }
  }
}
</script>
<style scoped>
.total {
  font-size: 16px;
  font-weight: bolder;
}
.pagination-container {
  text-align: center;
  padding: 5px 0px;
}
</style>



