<template>
<div class="edit-aunt">
  <el-card style="overflow:auto">
    <div class="info">
      <div class="info-header">
        <header>基本信息</header>
        <div>
          <el-button size="small" type="primary" @click="update" v-if='!updateStatus'>修改</el-button>
          <el-button size="small" @click="submit" v-if='updateStatus'>保存</el-button>
        </div>
      </div>
      <div class="info-content">
        <div class="info-left">
          <upload-photo use="ayiPhoto" @uploadSucc="uploadSucc">
            <img v-if="AuntDetail.photo !== ''" :src="AuntDetail.photo" class="avatar needsclick">
            <i v-else class="el-icon-plus avatar-uploader-icon"
            style='width: 130px;
                  font-size:10px;
                  height: 130px;
                  line-height:130px;
                  border: 1px solid;
                  border-radius: 50%;'>
              <span>点击上传阿姨照片</span>
            </i>
          </upload-photo>
          <span v-if="updateStatus" style="font-size:10px;color:#999">点击头像可更换阿姨照片</span>
          <!-- <span class="company">{{AuntDetail.housework_company.title}}</span> -->
          <video :src="AuntDetail.video" controls="controls" height="120px" style="margin-top:10px;width:230px" v-if="updateStatus ? false : true">
            您的浏览器不支持 video 标签。
          </video>
          <upload-video style="margin-top:15px" use='ayiVideo' @uploadSucc="uploadSucc" v-if="updateStatus ? true : false">
          </upload-video>
          <!-- <span class="company">
            {{AuntDetail.housework_company.title}}
          </span> -->
          <span class="credit">信用值:100</span>
        </div>
        <div class="info-center-left">
          <div>
            <span>阿姨姓名</span><el-input v-model="AuntDetail.title" disabled></el-input>
          </div>
          <div>
            <span>所属公司</span><el-input v-model="AuntDetail.housework_company.title" disabled></el-input>
          </div>
          <!-- <div>
            <span>编号</span><el-input v-model="code" disabled></el-input>
          </div> -->
          <div>
            <span>服务类别</span>
              <el-select v-model="AuntDetail.service_type_id"
                          :disabled='updateStatus ? false : true'
                          placeholder="请选择服务类型"
                          @change="getTag()"
                          >
                <el-option
                  v-for="item in serveTypeOptions"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
          </div>
          <div>
            <span>阿姨状态</span>
              <el-select v-model="AuntDetail.service_status"
                        :disabled='updateStatus ? false : true'
                        placeholder="请选择服务状态"
                        >
                <el-option
                  v-for="item in optionsStatus"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
          </div>
          <div>
            <span>手机号码</span>
            <el-input v-model="AuntDetail.phone"
                      :disabled='updateStatus ? false : true'
                      placeholder="请输入手机号码"
                      >
            </el-input>
          </div>
          <div>
            <span>自我介绍</span>
            <el-input type="textarea"
                      :disabled='updateStatus ? false : true'
                      :autosize="{ minRows: 4, maxRows: 6}"
                      placeholder="请填写自我介绍"
                      v-model="AuntDetail.self_intro">
            </el-input>
          </div>
        </div>
        <div class="info-center-right">
          <div>
            <span>身份证号</span>
            <el-input disabled v-model="AuntDetail.id_card"></el-input>
          </div>
          <div>
            <span>出生日期</span>
            <el-input disabled v-model="AuntDetail.birthday.split(' ')[0]"></el-input>
          </div>
          <div>
            <span>年龄</span>
            <el-input disabled v-model="AuntDetail.Age"></el-input>
          </div>
          <div>
            <span>学历</span>
            <el-select v-model="AuntDetail.education"
                        placeholder="请选择学历"
                        :disabled='updateStatus ? false : true'>
              <el-option
                v-for="item in educationOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div>
            <span>籍贯</span>
            <div style="width: 100%;">
              <el-cascader v-model="bir_addr"
                            v-if='bir_addr.length !== 0'
                            :props="props"
                            style="width: 100%;"
                            :disabled='updateStatus ? false : true'
                            @change="handleChangePlace">
              </el-cascader>
            </div>
          </div>
          <div>
            <span>现居地</span>
            <div style="width: 100%;">
              <div style="width:100%">
                <el-cascader :props="props"
                              v-if='Live.length !== 0'
                              v-model="Live" style="width: 100%;margin-bottom: 1px;"
                              :disabled='updateStatus ? false : true'
                              @change="handleChangeLive">
                </el-cascader>
              </div>
              <el-input type="textarea"
                        :autosize="{ minRows: 2, maxRows: 4}"
                        placeholder="请输入现居地址"
                        :disabled='updateStatus ? false : true'
                        v-model="AuntDetail.addr">
              </el-input>
            </div>
          </div>
        </div>
        <div class="info-right">
          <div>
            <span>等级</span>
            <el-select v-model="AuntDetail.level_id" placeholder="请选择等级" @change='changeLevel'  :disabled='updateStatus ? false : true'>
              <el-option
                v-for="item in levelOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div>
            <span>经验</span>
            <el-input v-model="AuntDetail.work_experience"
                      placeholder="（单位：个月）"
                      :disabled='updateStatus ? false : true'></el-input>
          </div>
          <div>
            <span>薪资</span>
            <el-input v-model="AuntDetail.salary" placeholder="请输入薪资" disabled></el-input>
          </div>
          <div>
            <span>账户总额</span>
            <el-input v-model="creditCode" disabled></el-input>
          </div>
          <div>
            <span>账户余额</span>
            <el-input v-model="balance" disabled></el-input>
          </div>
          <div>
            <span>银行卡号</span>
            <el-input v-model="AuntDetail.bankcard_number" placeholder="暂无" disabled></el-input>
          </div>
        </div>
      </div>
    </div>
  </el-card>
   <el-card style="overflow:auto;margin-top:30px">
    <div class="jobSkill">
      <h2>工作技能</h2>
      <div>
        <span>技能</span>
        <Tag :TagsArr="skillTags"
              :model='inputValue'
              :inputShowCondition="inputVisible"
              :updateStatus='updateStatus'
              :serviceTypeId="AuntDetail.service_type_id"
              type='技能'></Tag>
      </div>
      <div>
        <span>性格</span>
        <Tag :TagsArr="characterTags"
              :model='CharaValue'
              :inputShowCondition='Chara'
              :updateStatus='updateStatus'
              :serviceTypeId="AuntDetail.service_type_id"
              type='性格'></Tag>
      </div>
      <div>
        <span>证书</span>
        <div class="certificateDiv">
          <span>
            <Tag :TagsArr="certificateTags"
                  :model='CertificateValue'
                  :inputShowCondition="CertificateVisible"
                  :updateStatus='updateStatus'
                  :serviceTypeId="AuntDetail.service_type_id"
                  type='证书'></Tag>
          </span>
          <div style="margin-top:30px">
            <upload-images use="cert" :updateStatus="updateStatus" :fileList='certificateFileList'
              :isShowFileList="true" @uploadSucc="uploadSucc" @removeFile="removeFile"></upload-images>
          </div>
        </div>
      </div>
      <div>
        <span>工作风采</span>
        <div style="border-bottom:none">
          <upload-images use="work"
                          :updateStatus="updateStatus"
                          :fileList="workPhotosFileList"
                          :isShowFileList="false"
                          @uploadSucc="uploadSucc"
                          @removeFile="removeFile">
          </upload-images>
        </div>
      </div>
      <div>
        <span class="idcard-label">
          身份证信息
        </span>
        <div id="id-card-content">
          <div class="id-card-content-item">
            <el-image
              class="id-card-image"
              :src="AuntDetail.id_hold"
              :fit="fit"></el-image>
          </div>
          <div class="id-card-content-item">
            <el-image
              class="id-card-image"
              :src="AuntDetail.id_back"
              :fit="fit"></el-image>
          </div>
        </div>
      </div>
    </div>
   </el-card>
   <RecordList :id='id'></RecordList>
</div>
</template>
<script>
  import {getTypeService,
    getStatusService,
    getSkillService,
    getCharacterService,
    getCertificateService,
    getPlaceService,
    getDetailService,
    updateAunt,
    removeImage,
    getLevelService,
    getBasicInformation
  } from '../../service/AuntManage'
  import { uploadImage } from '../../service/loginServer.js'
  import { transEducationData, AuntDetail } from './AuntDetailConfig.js'
  import RecordList from './RecordList.vue'
  import UploadImages from '../../components/UploadImages.vue'
  import UploadPhoto from '../../components/UploadPhoto.vue'
  import UploadVideo from '../../components/UploadVideo.vue'
  import Tag from './Tag.vue'
  import { GetAge } from '../../assets/common/utils.js'
  export default {
    components: {RecordList, Tag, 'upload-images': UploadImages, 'upload-photo': UploadPhoto, 'upload-video': UploadVideo},
    data () {
      return {
        fit: 'contain',
        pid: '-1',
        bir_addr: [],
        Live: [],
        code: '',
        creditCode: '0',
        balance: '0',
        props: {},
        dialogVisible: false,
        updateStatus: false,
        dialogImageUrl: '',
        AuntDetail: AuntDetail,
        optionsStatus: [],
        educationOptions: [],
        addressOptions: [],
        placeOptions: [],
        levelOptions: [],
        inputValue: {value: ''},
        inputVisible: {value: false},
        CharaValue: {value: ''},
        Chara: {value: false},
        CertificateVisible: {value: false},
        CertificateValue: {value: ''},
        skillTags: [],
        characterTags: [],
        certificateTags: [],
        certificateFileList: [],
        workPhotosFileList: [],
        certificateImages: [],
        workPhotos: [],
        ossHost: 'https://deixian.oss-cn-beijing.aliyuncs.com',
        serveTypeOptions: '',
        id: '',
        removeImgIds: []
      }
    },
    created () {
      this.$store.dispatch('commitChangeTopTitles', ['阿姨管理', '阿姨信息', '阿姨详情'])
      this.id = this.$route.params.id
      Promise.all([this.getTypeOption(), this.getStatusOption(), this.getCharacterTags(), this.getEducations()])
        .then(res => {
          this.getAuntDetail()
        })
    },
    methods: {
      getTag () {
        this.AuntDetail.level_id = null
        this.getLevel()
        this.getSkillTag()
        this.getCertificateTags()
      },
      // 移除图片
      removeFile (file) {
        // 从filelist展示列表中移除
        this.certificateFileList.forEach((element, index) => {
          if (element.url === file.file.url) {
            this.certificateFileList.splice(index, 1)
          }
        })
        this.workPhotosFileList.forEach((element, index) => {
          if (element.url === file.file.url) {
            this.workPhotosFileList.splice(index, 1)
          }
        })
        if (file.file.id) {
          this.removeImgIds.push(file.file.id)
        } else {
          this.certificateImages.forEach((element, index) => {
            if (element === file.file.url) {
              this.certificateImages.splice(index, 1)
            }
          })
          this.workPhotos.forEach((element, index) => {
            if (element === file.file.url) {
              this.workPhotos.splice(index, 1)
            }
          })
        }
      },
      // 获取阿姨详情
      getAuntDetail () {
        return new Promise((resolve, reject) => {
          getDetailService(this.$route.params.id).then((res) => {
            if (res.status === 'success') {
              this.code = res.data.id
              res.data.service_type_id = res.data.service_type.id
              this.Live = [res.data.addr_info[0].code, res.data.addr_info[1].code]
              this.bir_addr = [res.data.region_info[0].code, res.data.region_info[1].code]
              console.log(GetAge(res.data.id_card))
              res.data.Age = GetAge(res.data.id_card)
              let that = this
              this.props = {
                lazy: true,
                lazyLoad (node, resolve) {
                  const { level } = node
                  let param = {}
                  if (node.data) {
                    that.pid = node.data.id
                  }
                  param.id = that.pid
                  getPlaceService(param).then((res) => {
                    let resPlaceArray = res.data
                    resPlaceArray.forEach(element => {
                      element.value = element.code
                      element.id = element.id
                      element.label = element.name
                      element.leaf = level >= 1
                    })
                    resolve(resPlaceArray)
                  })
                }
              }
              res.data.skills_label.forEach((item) => {
                item.status = 'choosed'
              })
              res.data.nature_label.forEach((item) => {
                item.status = 'choosed'
              })
              if (res.data.cert_label) {
                res.data.cert_label.forEach((item) => {
                  item.status = 'choosed'
                })
              }
              this.skillTags = res.data.skills_label
              this.certificateTags = res.data.cert_label
              this.characterTags = res.data.nature_label
              res.data.cert_image.forEach((item) => {
                item.url = item.img_url
              })
              res.data.work_style.forEach((item) => {
                item.url = item.img_url
              })
              this.workPhotosFileList = res.data.work_style
              this.certificateFileList = res.data.cert_image
              this.AuntDetail = res.data
              this.AuntDetail.bankcard_number = this.AuntDetail.bankcard
            }
            this.getLevel()
            resolve(res)
          })
        })
      },
      // 上传照片
      uploadPhoto (param) {
        let fd = new FormData()
        fd.append('photo_id', param.file)
        uploadImage(fd).then(res => {
          this.AuntDetail.photo = res.data.file_path
        })
      },
      // 获取服务状态
      getStatusOption () {
        return new Promise((resolve, reject) => {
          getStatusService().then((res) => {
            let statusTempArray = []
            let resStatusobj = res.data
            for (const key in resStatusobj) {
              let obj = {}
              obj.label = resStatusobj[key]
              obj.value = key
              statusTempArray.push(obj)
            }
            this.optionsStatus = statusTempArray
            resolve(res)
          })
        })
      },
      // 获取学历列表
      getEducations () {
        getBasicInformation().then(res => {
          let { educationList } = res.data
          this.educationOptions = transEducationData(educationList)
        })
      },
      // 验证参数
      verify () {
        let flag = true
        if (!this.AuntDetail.photo) {
          this.$message('请上传头像')
          return
        }
        if (!this.AuntDetail.title) {
          this.$message('请输入姓名')
          return
        }
        if (!this.AuntDetail.service_type_id) {
          this.$message('请选择服务类型')
          return
        }
        if (!this.AuntDetail.phone) {
          this.$message('请输入手机号')
          return
        }
        if (!(/^1(3|4|5|7|8)\d{9}$/.test(this.AuntDetail.phone))) {
          this.$message('手机号格式错误')
          return
        }
        if (!this.AuntDetail.id_card) {
          this.$message('请填写身份证号')
          return
        }
        var cardreg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/
        if (!cardreg.test(this.AuntDetail.id_card)) {
          this.$message('身份证格式错误')
          return
        }
        if (!this.AuntDetail.education) {
          this.$message('请输入选择学历')
          return
        }
        if (!this.AuntDetail.birthplace_province_id) {
          this.$message('请选择籍贯')
          return
        }
        if (!this.AuntDetail.addr_province) {
          this.$message('请选择现居地地址')
          return
        }
        if (!this.AuntDetail.work_experience) {
          this.$message('请填写工作经验')
          return
        }
        if (!this.AuntDetail.level_id) {
          this.$message('请选择等级')
          return
        }
        if (!this.AuntDetail.nature || this.AuntDetail.nature === '') {
          this.$message('请选择选择性格标签')
          return
        }
        return flag
      },
      // 整理参数
      sorting () {
        let serviceLabel = []
        let characterLabel = []
        this.skillTags.forEach((item) => {
          if (item.status === 'choosed') {
            serviceLabel.push(item.id)
          }
        })
        this.certificateTags.forEach((item) => {
          if (item.status === 'choosed') {
            serviceLabel.push(item.id)
          }
        })
        this.characterTags.forEach((item) => {
          if (item.status === 'choosed') {
            characterLabel.push(item.id)
          }
        })
        this.AuntDetail.service_label = serviceLabel.join(',')
        this.AuntDetail.nature = characterLabel.join(',')
        let certificateImages = [...this.certificateImages]
        certificateImages = certificateImages.map((item) => {
          item = item.split(this.ossHost)[1]
          console.log(item)
          return item
        })
        let workPhotos = [...this.workPhotos]
        workPhotos = workPhotos.map((item) => {
          item = item.split(this.ossHost)[1]
          console.log(item)
          return item
        })
        this.AuntDetail.cert_image = certificateImages
        this.AuntDetail.work_style = workPhotos
        let param = {...this.AuntDetail}
        let photoPath = this.AuntDetail.photo
        let videoPath = this.AuntDetail.video
        if (photoPath.indexOf(this.ossHost) !== -1) {
          param.photo = photoPath.split(this.ossHost)[1]
        } else {
          param.photo = photoPath
        }
        if (videoPath.indexOf(this.ossHost) !== -1) {
          param.video = videoPath.split(this.ossHost)[1]
        } else {
          param.video = videoPath
        }
        console.log(param)
        if (param.id_hold) {
          param.id_hold = param.id_hold.split(this.ossHost)[1]
        }
        if (param.id_back) {
          param.id_back = param.id_back.split(this.ossHost)[1]
        }
        delete param.live_home
        return param
      },
      // 提交表单
      submit () {
        let sendParams = this.sorting()
        if (!this.verify(sendParams)) { return }
        // 删除证书图片工作风采图
        console.log(this.removeImgIds)
        let params = {cert_ids: this.removeImgIds}
        removeImage(params).then((res) => {
          updateAunt(sendParams).then((res) => {
            if (res.status === 'success') {
              this.$message({type: 'success', message: '修改成功'})
              this.$router.push('/AuntList')
            }
          })
        })
      },
      // 修改修改状态
      update () {
        this.updateStatus = true
        this.getSkillTag()
        this.getCharacterTags()
        this.getCertificateTags()
      },
      // 获取等级
      getLevel () {
        return new Promise((resolve, reject) => {
          let params = {
            type: this.AuntDetail.service_type_id
          }
          getLevelService(params).then((res) => {
            let salaryToLevel = {}
            let levelOptions = res.data
            levelOptions.forEach(element => {
              salaryToLevel[element.salary] = element.id
              element.value = element.id
              element.label = element.level_name
            })
            this.salaryToLevel = salaryToLevel
            this.levelOptions = levelOptions
            console.log(this.salaryToLevel)
            console.log(this.AuntDetail.salary)
            this.AuntDetail.level_id = this.salaryToLevel[this.AuntDetail.salary]
            resolve(res)
          })
        })
      },
      // 获取服务类型列表
      getTypeOption () {
        return new Promise((resolve, reject) => {
          getTypeService().then((res) => {
            let resTypeArray = res.data.items
            resTypeArray.forEach(element => {
              element.value = element.id
              element.label = element.title
            })
            let serveTypeOptions = resTypeArray
            this.serveTypeOptions = serveTypeOptions
            resolve(res)
          })
        })
      },
      // 选择等级获取薪资
      changeLevel (param) {
        this.levelOptions.forEach((item) => {
          if (item.id === param) {
            this.AuntDetail.salary = item.salary
          }
        })
      },
      // 获取所有技能标签
      getSkillTag () {
        let param = {}
        param.category = 'skills-label'
        param.service_type_id = this.AuntDetail.service_type_id
        return new Promise((resolve, reject) => {
          getSkillService(param).then((res) => {
            let resSkillArray = res.data.items
            if (this.AuntDetail.skills_label) {
              resSkillArray.forEach((item) => {
                this.AuntDetail.skills_label.forEach((item2) => {
                  if (item.id === item2.id) {
                    item.status = 'choosed'
                  }
                })
              })
            }
            this.skillTags = resSkillArray
            resolve(res)
          })
        })
      },
      // 获取所有性格标签
      getCharacterTags () {
        return new Promise((resolve, reject) => {
          getCharacterService().then((res) => {
            let resCharacterArray = res.data
            if (this.AuntDetail.nature_label) {
              resCharacterArray.forEach((item) => {
                this.AuntDetail.nature_label.forEach((item2) => {
                  if (item.id === item2.id) {
                    item.status = 'choosed'
                  }
                })
              })
            }
            this.characterTags = resCharacterArray
            resolve(res)
          })
        })
      },
      // 获取所有证书标签
      getCertificateTags () {
        let param = {}
        param.category = 'certificate-label'
        param.service_type_id = this.AuntDetail.service_type_id
        return new Promise((resolve, reject) => {
          getCertificateService(param).then((res) => {
            let resCertificateArray = res.data.items
            if (this.AuntDetail.cert_label) {
              resCertificateArray.forEach((item) => {
                this.AuntDetail.cert_label.forEach((item2) => {
                  if (item.id === item2.id) {
                    item.status = 'choosed'
                  }
                })
              })
            }
            let certificateTags = resCertificateArray
            this.certificateTags = certificateTags
            resolve(res)
          })
        })
      },
      // 选择籍贯
      handleChangePlace (value) {
        this.AuntDetail.birthplace_province_id = value[0]
        this.AuntDetail.birthplace_city_id = value[1]
      },
      // 选择出生地
      handleChangeLive (value) {
        this.AuntDetail.addr_province = value[0]
        this.AuntDetail.addr_city = value[1]
      },
      uploadSucc (file) {
        this.ossHost = file.host
        console.log(this.ossHost)
        if (file.use === 'cert') {
          // 展示证书图片filelist
          this.certificateFileList.push({url: file.path})
          console.log(this.certificateFileList)
          // 传给服务器的证书图片list
          this.certificateImages.push(file.path)
          console.log(this.certificateImages)
        } else if (file.use === 'work') {
          this.workPhotosFileList.push({url: file.path})
          this.workPhotos.push(file.path)
        } else if (file.use === 'ayiVideo') {
          this.AuntDetail['video'] = file.path
          console.log(this.AuntDetail)
        } else {
          this.AuntDetail.photo = file.path
        }
      }
    }
  }
</script>
